import logger from '@accedo/xdk-log';
import { klass, core, SystemConstants, util, HTML5System, MouseOnOff } from '@accedo/xdk-core';
import System from '@accedo/xdk-device-freeview-html5-contrib/esm/System';
import Network from '@accedo/xdk-device-freeview-html5-contrib/esm/Network';
import TVKey from './TVKey';

const { logError, debug } = logger({ logTag: '[freesat/System]' });
const { stringifyWithoutDom } = util;

const ExtendedSystem = klass.create(
    System,
    {},
    {
        init() {
            this._network = new Network();
            this._key = new TVKey();
            this._mouse = new MouseOnOff();
        }
    }
);

export default ExtendedSystem;
