/* eslint-disable no-underscore-dangle */

import { klass, AbstractDevicePackage, StorageManager, DeviceExtensionManager } from '@accedo/xdk-core';
import logger from '@accedo/xdk-log';
import System from './System';

const { debug } = logger({ logTag: '[freesat/DevicePackage]' });

const DevicePackage = klass.create(
    AbstractDevicePackage,
    {},
    {
        async setup() {
            debug('Setting up DevicePackage.');

            debug('init Storage Manager.');
            this._storageManager = StorageManager.singleton();

            debug('init Device Extension Manager.');
            this._extensionManager = DeviceExtensionManager.singleton();

            debug('init System.');
            const system = new System();

            this._system = system;

            debug('Return system.isReady');

            const ready = this._system && this._storageManager && this._extensionManager;

            return Promise.resolve(!!ready);
        }
    }
);

export default DevicePackage;
