/* eslint-disable no-underscore-dangle */
import { BrowserTVKey, environment, klass, util, keyMap, vKey } from '@accedo/xdk-core';

const { clone } = util;
const { KEYBOARD_KEY, KEYBOARD_CHAR } = keyMap;

const TVKey = klass.create(
    BrowserTVKey,
    {
        KEY_SUBTITLE: { id: 'device:vkey:subtitle' },
        KEY_SEARCH: { id: 'device:vkey:search' }
    },
    {
        _bubbleNativeKeyEvent: false,
        _preventDefaultKeyAction: true,

        _keyHandler(event) {
            if (environment.SYSTEM.KEY_BLOCKED) {
                return;
            }

            this._super(event);
        },

        init() {
            const keyMapping = clone(KEYBOARD_KEY, true);
            const charMapping = clone(KEYBOARD_CHAR, true);

            keyMapping.VKey[461] = vKey.BACK;

            keyMapping.VKey[403] = vKey.RED;
            keyMapping.VKey[404] = vKey.GREEN;
            keyMapping.VKey[405] = vKey.YELLOW;
            keyMapping.VKey[406] = vKey.BLUE;

            keyMapping.VKey[417] = vKey.FF;
            keyMapping.VKey[412] = vKey.RW;
            keyMapping.VKey[415] = vKey.PLAY;
            keyMapping.VKey[19] = vKey.PAUSE;
            keyMapping.VKey[413] = vKey.STOP;
            keyMapping.VKey[463] = vKey.PLAY_PAUSE;

            keyMapping.VKey[104] = this.constructor.KEY_SEARCH;
            keyMapping.VKey[460] = this.constructor.KEY_SUBTITLE;

            keyMapping.VKey[424] = vKey.PREV;
            keyMapping.VKey[425] = vKey.NEXT;

            this._initKeyMapping(keyMapping, charMapping);
            this.initKeyHandling();
        }
    }
);

export default TVKey;
